

#how{
    
    background-color: $secondaryColor;
    color: $primaryColor;

    & .container-full{
        background-color: $secondaryColor;
    }
}

.how__content{
margin-top: 60px;
}

.how__content > * + * {
    margin-top: 40px;
}

.how__row{
    display: flex;
    justify-content: space-between;
}


.how__item{
    display: flex;
    position: relative;


    & span{
        position: absolute;
        top:0;
        left:0;

font-weight: 600;
font-size: 56px;
line-height: 1.05;


color: rgba(255, 255, 255, 0.5);
    }

    & p{
        padding-left: 70px;
        max-width: 560px;
    }
}


.how__banner {
    position: relative;
    padding:60px;
    margin-top: 60px;
    
    background-color: $bg-color;
    background-image: url(../images/how/bg-how.png);
    background-position: right bottom;
    background-repeat: no-repeat;

    border-radius: 60px;
    color: $thirdColor;
    font-size: 24px;

    & .title-h3{
        color: $secondaryColor;
        max-width: 406px;
    }
}

.how__banner-content{
    max-width: 600px;

    & .button{
        margin-top: 40px;
        display: inline-block;
    }

    
}

.how__banner-content > * + * {
margin-top: 20px;
}

.how__banner-info{
    & p:not(:first-child){
        max-width: 525px;
    }
}

.how__banner-info > * + * {
    margin-top: 20px;
}

.how__image{
position: absolute;
bottom:0;
right:0;
}