#benefits {
  color: $thirdColor;

  & .container-full {
    background-image: url(../images/benefits/benefits-bg1.png);
    background-repeat: no-repeat;
    background-position: top center;
  }
}

.benefits__wrapper > * + * {
  margin-top: 60px;
}

.benefits__header {
  max-width: 450px;
}

.benefits__row {
  display: flex;
}

.benefits__row > * + * {
  margin-left: 30px;
}

.benefits-item-left,
.benefits-item-right {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding-top: 60px;
  padding-right: 60px;
  padding-left: 60px;

  border-radius: 50px;
  font-size: 28px;
  color: $primaryColor;

  min-height: 668px;
}

.benefits-item-left {
  background-image: url(../images/benefits/benefits-bg-1.png);

  & span {
    color: #bcc2ce;
  }
}

.benefits-item-right {
  background-image: url(../images/benefits/benefits-bg-2.png);

  & span {
    color: #fa90d1;
  }
}

.benefits-item__img {
  margin-left: auto;
  margin-right: auto;
}
