#header {
  padding-top: 25px;

  min-height: 1320px;

  background-image: url(../images/header/header-bg.png);
  background-repeat: no-repeat;
  background-position: bottom center;
}

.header__nav-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  & .button {
    background-color: $thirdColor;
  }
}

.header__content{
  padding-top: 75px;
  color:$thirdColor;

  

  & .title-h1{
    text-align: center;
    max-width: 740px;

    margin-left: auto;
    margin-right: auto;
  }
}

.header__content > * + * {
  margin-top: 30px;
}

.header__button{
  max-width: 640px;
  margin-left: auto;
  margin-right: auto;

  text-align: center;

  & a{
    display: inline-block;
    
  }
}

.header__button > * + * {
margin-top: 40px;
}

.header__marquee{
  padding-top: 25px;
  padding-bottom: 25px;

  background-color:#BCFF50;

  color:$thirdColor;
  
  overflow: hidden;

}

@keyframes moveSlideshow {
  100% { 
    transform: translateX(-3000px);  
  }
}

.marquee__content{
  display: flex;
  animation: moveSlideshow 60s linear infinite;
}

.marquee__content > * + * {
  margin-left: 16px;
}


.marquee-item{
  display: flex;
  align-items: center;

  flex-shrink: 0;
}

.marquee-item > * + * {
  margin-left: 16px;
}

