#multiply {
  & .container-full {
    padding-bottom: 0;
    min-height: 975px;
    background-color: #4924a9;

    background-image: url(../images/multiply/multiply-bg.png);
    background-repeat: no-repeat;
    background-position: center;
  }
}

.multiply__header {
  text-align: center;
  max-width: 650px;
  margin-left: auto;
  margin-right: auto;

  & .button {
    display: inline-block;
    background-color: $primaryColor;
    color: $thirdColor;
  }
}

.multiply__header > * + * {
  margin-top: 40px;
}
