#getting-started {
  overflow: hidden;
  background-color: #6037cc;
}

#getting-started .first__section {
  & .container-full {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: #6037cc;
    background-image: url(../images/getting-started/first-bg.png);
    background-repeat: no-repeat;
    background-position: top center;

    margin-top: 0;
  }

  & .wrapper__container {
    min-height: 885px;
  }
}

#getting-started .second__section {
  position: relative;
  width: 115%;

  transform: translate(-7%, -510px) rotate(-11deg);

  & .container-full {
    border-top-left-radius: 0;
    border-top-right-radius: 0;

    margin-top: 0;
    background-color: blue;
  }

  & .wrapper__container {
    min-height: 600px;
    transform: rotate(11deg);
    display: flex;
    align-items: flex-end;
  }
}

#getting-started .third__section {
  margin-top: -870px;
  & .container-full {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: #7f4dfe;

    margin-top: 0;
  }

  & .wrapper__container {
    min-height: 910px;

    display: flex;
    align-items: flex-end;
  }

  & .section-content span {
    color: #ffdf38;
  }
}

#getting-started .section-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

#getting-started .section-content {
  max-width: 498px;

  & h2 {
    color: #fa90d1;
  }
}

#getting-started .section-content > * + * {
  margin-top: 30px;
}
