#partners {
  & .container-full {
    background-image: url(../images/partners/partners-bg.png);
    background-color: #4690ff;
    background-repeat: no-repeat;
    background-position: top center;

    min-height: 2280px;
  }
}

.partners__header {
  max-width: 600px;
}

.partners__banner-row {
  display: flex;
  align-items: flex-end;
}

.partners__banner-left {
  max-width: 470px;
  padding-bottom: 60px;
}

.partners__banner {
  position: relative;
  margin-top: 60px;
  padding-top: 60px;
  padding-left: 60px;

  color: $thirdColor;

  background-color: $primaryColor;

  border-radius: 50px;
}

.partners__banner-content {
  display: flex;
  flex-direction: column;

  margin-top: 40px;
}

.partners__banner-content > * + * {
  margin-top: 40px;
}

.partners-steps {
  display: flex;

  & p {
    font-weight: 400;
    font-size: 24px;
    line-height: 1.3;
    padding-left: 40px;
  }

  & span {
    font-weight: 600;
    font-size: 40px;

    color: #519ef8;
  }
}

.partners__cloud {
  position: absolute;
  bottom: -100px;
  right: -180px;
}

.partners__list {
  margin-top: 80px;
}

.pattern-item {
  max-width: 262px;
  border-radius: 50px;
}

.pattern__row {
  display: flex;
  align-items: center;
}

.pattern__row > * + * {
  margin-left: 30px;
}

.partners__pattern {
  margin-top: 60px;
}

.partners__pattern > * + * {
  margin-top: 30px;
}

.pattern__top {
  display: flex;
}

.pattern__top > * + * {
  margin-left: 30px;
}

.pattern__rows {
  display: flex;
  flex-wrap: wrap;
}

.pattern__rows > * {
  margin: 15px;
}

.partners__quote {
  margin-top: 70px;
  max-width: 574px;

  margin-left: auto;
  margin-right: auto;
  & span {
    display: block;
    color: rgba(255, 255, 255, 0.6);
    font-size: 24px;
  }
}

.partners__quote > * + * {
  margin-top: 30px;
}
